import Vue from "vue";
import VueRouter from "vue-router";
import Home from "@/view/home";
import product from "@/view/product/product";
import information from "@/view/information/information";
import Layout from "@/components/Layout/index";
import aboutUs from "@/view/aboutUs/aboutUs";

Vue.use(VueRouter);

const router = new VueRouter({
  mode: 'history',
  routes: [
    {
      path: "/",
      component: Layout,
      children: [
        { path: "/", redirect: "home" },
        { path: "/home", name: "Home", component: Home },
        { path: "/product", name: "product", component: product },
        { path: "/information", name: "information", component: information },
        { path: "/aboutUs", name: "aboutUs", component: aboutUs },
        {
          path: "/mall",
          name: "mall",
          component: () => import("@/view/mall/mall.vue"),
        },
      ],
    },
    {
      path: "/firmware",
      component: Layout,
      children: [
        { path: "", redirect: "selectProduct" }, // 
        { path: "selectProduct", name: "selectProduct", component: () => import("@/view/firmware/selectProduct") },
        { path: "selectPlatform", name: "selectPlatform", component: () => import("@/view/firmware/selectPlatform") },
        { path: "selectVersion", name: "selectVersion", component: () => import("@/view/firmware/selectVersion") },
      ],
    },
    {
      path: "/newsDetail",
      name: "newsDetail",
      component: () => import("@/view/information/newsDetail.vue"),
    },
    {
      path: "/appleAppDetail",
      name: "appleAppDetail",
      component: () => import("@/view/information/appleAppDetail.vue"),
    },
  ],
});

export default router;



