<template>
    <div class="header-main">
        <el-row>
            <el-col :span="24">
                <div class="grid-content bg-purple-dark">
                    <div class="header">
                        <div class="logo" @click="reloadHome">
                            <div><img src="@/assets/white.png" alt=""></div>
                            <div><span class="txt">MiliTools</span></div>
                        </div>
                        <div class="navigation">
                            <el-tabs @tab-click="handleClick" v-model="_path" class="tabs" :key="currentLanguage">
                                <el-tab-pane v-for="(item) in tabData" :key="item.path" :label="item.name"
                                    :name="item.path" class="tab-list" width="300px"></el-tab-pane>
                            </el-tabs>
                        </div>
                        <div class="search">
                            <div class="i18n" @click="i18nStatusTab">
                                <div style="color: #fff;"> {{ i18nStatus == 'zh' ? '' : 'EN' }} / <span
                                        style="color: #999999;font-size: 14px;">{{ i18nStatus == 'en' ? '' : 'EN'
                                        }}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import Cookies from 'js-cookie'

export default {

    data() {
        return {
            _path: '/home',
            i18nStatus: Cookies.get('language') ? Cookies.get('language') : 'zh',
            tabData: [
                { name: this.$t('header.homePage'), id: 0, path: '/home' },
                { name: this.$t('header.product'), id: 1, path: '/product' },
                { name: this.$t('header.message'), id: 2, path: '/information' },
                { name: this.$t('header.firmware'), id: 3, path: '/firmware' },
                { name: this.$t('header.Mall'), id: 4, path: '/mall' },
                { name: this.$t('header.aboutUs'), id: 5, path: '/aboutUs' }
            ],
            fileName: "",
            currentLanguage: true
        }
    },
    methods: {
        reloadHome() {
            window.location.href = '/home';
        },
        handleClick() {
            this.$router.push(this._path)
        },
        i18nStatusTab() {
            this.i18nStatus = this.i18nStatus == "zh" ? 'en' : 'zh'
            this.currentLanguage = !this.currentLanguage
            this.$i18n.locale = this.i18nStatus
            this.$store.commit('SET_LANGUAGE', this.i18nStatus);
            // tabData
            this.tabData.forEach(item => {
                switch (item.path) {
                    case '/home':
                        item.name = this.$t('header.homePage');
                        break;
                    case '/product':
                        item.name = this.$t('header.product');
                        break;
                    case '/information':
                        item.name = this.$t('header.message');
                        break;
                    case '/firmware':
                        item.name = this.$t('header.firmware');
                        break;
                    case '/mall':
                        item.name = this.$t('header.Mall');
                        break;
                    case '/aboutUs':
                        item.name = this.$t('header.aboutUs');
                        break;
                    default:
                        break;
                }
            });
        }
    },
    computed: {
        language() {
            return this.$store.getters.language // 
        },
    },

    watch: {
        '$route': {
            immediate: true,
            handler(to, from) {
                console.log(to, from)
                this._path = to.path
            }
        },
    },
}
</script>

<style scoped lang="less">
.header-main {
    width: 100%;
}

.el-row {
    margin-bottom: 20px;

    &:last-child {
        margin-bottom: 0;
    }
}

.bg-purple-dark {
    min-width: 1400px;
    max-width: 100%;

    background: #000000;

    .header {
        margin: 0 auto;
        width: 1400px;
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .logo {
            margin-top: 6px;
            cursor: pointer;
            color: #FFFFFF;
            display: flex;
            align-items: center;

            img {
                margin-top: -4px;
                height: 40px;
            }

            .txt {
                margin-left: 14px;
                font-size: 15px;
            }

        }

        .navigation {
            margin-left: 120px;
            height: 38px;

            .tab-list {
                width: 200px;
            }

            /deep/.el-tabs__item {
                font-size: 16px;
                color: #FFFFFF;
            }

            /deep/.el-tabs__item:hover {
                color: #527fda;
            }

            /deep/ .is -active {
                color: #FFFFFF;
                font-weight: bold;

            }

            /deep/.el-tabs__active-bar {
                background-color: #FFFFFF;
            }

            /deep/.el-tabs__nav-wrap::after {
                height: 0px;
            }

            /deep/.el-tabs__item {
                padding-right: 40px;
                font-size: 14px;
            }

        }

        .search {
            margin-left: auto;
            margin-right: 20px;
            cursor: default;

            .i18n {
                cursor: pointer;
                font-size: 14px;
                color: #FFFFFF;
                line-height: 40px;
            }
        }

    }
}
</style>


