import requests from "./request";

export const getRenew = () => {
  return requests({
    url: "/news/renew",
    method: "get",
  });
};

export const getprisonBreak = () => {
  return requests({
    url: "/news/prisonBreak",
    method: "get",
  });
};

export const getTutorial = () => {
  return requests({
    url: "/news/Tutorial",
    method: "get",
  });
};

export const getSkill = () => {
  return requests({
    url: "/news/Skill",
    method: "get",
  });
};

export const getAppleNews = () => {
  return requests({
    url: "/news/appleNews",
    method: "get",
  });
};

export const getAppleDetail = (params) => {
  return requests({
    url: "/news/AppleNewsDetail",
    method: "get",
    params: params,
  });
};

export const getAppDetail = (params) => {
  return requests({
    url: "/news/AppleAppDetail",
    method: "get",
    params: params,
  });
};



