<template>
    <div class="bottom-main">
        <div class="Divider"></div>
        <el-row>
            <el-col :span="24">
                <div class="bg-bottom-dark">
                    <div class="bottom-conter">
                        <div class="bottom-conter-left">
                            <div class="bottom-conter-title">
                                <div v-for="(item, index)  in bottomTitle" :key="index" class="txt"
                                    @click="path(item.name)">
                                    <span class="menu-text"> {{ item.name }} </span>
                                    <span class="margin" v-if="index !== bottomTitle.length - 1">|</span>
                                </div>
                            </div>
                            <div class="corporateinformation">
                                <div>@2017 - 2024 Shenzhen Linyou Technology Co., Ltd</div>
                                <div></div>
                            </div>
                            <div class="corporateinformation">
                                <div>00000000000000</div>
                                <div>ICP000000000-8</div>
                            </div>
                        </div>
                        <!-- <div class="bottom-conter-right">
                            <div class="bottom-code"></div>
                        </div> -->
                    </div>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import Cookies from 'js-cookie'
import { mapState } from 'vuex';

export default {

    data() {
        return {
            bottomTitle: [],
            i18nStatus: Cookies.get('language'),

        }
    },
    created() {
        this.updateBottomTitle();
    },
    methods: {
        path(name) {
            if (name == 'MiliTools') {
                window.location.href = '/home';
            }
        },
        updateBottomTitle() {
            this.bottomTitle = [
                { name: 'MiliTools' },
                { name: this.$t('bottom.contactUs') },
                { name: this.$t('bottom.problemFeedback') },
                { name: this.$t('bottom.termsofuse') },
                { name: this.$t('bottom.privacyPolicy') },
            ];
        },
    },
    computed: {
        ...mapState(['language']),
    },
    watch: {
        language(newVal, oldVal) {
            console.log('Language changed. Updating bottom title...');
            if (newVal !== oldVal) {
                this.updateBottomTitle();
            }
        },
    },

}
</script>

<style scoped lang="less">
.bottom-main {
    width: 100%;
    margin-top: auto;
}

.Divider {
    height: 1px;
    background-color: #272727;
}

.el-row {
    margin-bottom: 20px;

    &:last-child {
        margin-bottom: 0;
    }
}

.bg-bottom-dark {
    min-width: 1400px;
    background: #000000;
    padding-top: 20px;

    .bottom-conter {
        display: flex;
        justify-content: space-between;
        width: 1200px;
        margin: 0 auto;

        .bottom-conter-left {
            margin: 50px 0 70px 0;

            .bottom-conter-title {
                display: flex;
                // justify-content: space-evenly;
                color: #666666;
                font-family: "Adobe Heiti Std";
                font-size: 13px;
                margin-bottom: 50px;
                // margin-left: 12px;

                .margin {
                    margin-left: 20px;
                }

                .txt {
                    margin-right: 20px;

                    .menu-text {
                        cursor: pointer;
                    }

                    .menu-text:hover {
                        color: #fff;
                    }
                }
            }

            .corporateinformation {
                color: #999999;
                display: flex;
                margin-top: 10px;
                // margin-left: 20px;
                // justify-content: space-evenly;
                font-size: 13px;

                div {
                    // margin-left: 10px;
                }
            }
        }

        .bottom-conter-right {
            width: 200px;
            height: 50px;
            // background-color: red;
        }
    }


}
</style>


