                        </div>
                        <div class="product-content-data">
                            <div class="product-content-data-title">MiLiTools {{$t("homePage.computerTerminal")}}</div>
                            <div class="product-content-data-txt">
                                {{$t("homePage.productCenter1")}}</div>
                            <div>
                                <el-button type="primary" round>{{$t("homePage.downloadNow")}}</el-button>
                                <!-- <el-button type="primary" round></el-button> -->
                            </div>
                        </div>
                    </div>
                </el-card>
