<script>
import { mapState } from 'vuex';

export default {

    data() {
        return {
            MenuList: [
                { id: 1, name: this.$t("product.sort1"), imageUrl: require('@/assets/product/diannao.png') },
                // { id: 2, name: 'MiLiTools', imageUrl: require('@/assets/product/shouji.png') },
                // { id: 3, name: 'MiLiTools', imageUrl: require('@/assets/product/touping.png') },
            ]
        }
    },
    methods: {
        updateBottomTitle() {
        this.MenuList = [
        { id: 1, name: this.$t("product.sort1"), imageUrl: require('@/assets/product/diannao.png') },
        ];
    },
    },
    computed: {
        ...mapState(['language']),
    },
    watch: {
    language(newVal, oldVal) {
        console.log('Language changed. Updating bottom title...');
        if (newVal !== oldVal) {
            this.updateBottomTitle();
        }
    },
},
    

}
</script>
<style scoped lang="less">
.main {
    height: 100%;
    width: 100%;
}

.product-function-diagram {
    width: 100%;
    background-color: #212121;
    height: 180px;

    .diagram-list {
        width: 900px;
        margin: 0 auto;
        display: flex;
        justify-content: space-evenly;
        color: #FFFFFF;
        font-size: 15px;

        .diagram-list-item {
            width: 200px;
            text-align: center;
            margin: 40px auto;

            .diagram-list-item-img {
                margin-bottom: 20px;
                height: 70px;

                img {
                    width: 80px;
                }
            }

        }
    }
}
// 
.Secondary-Menu-bg {
    height: 110px;
    background: #212121;
    display: flex;
    justify-content: center;
    min-width: 1200px;

    .Menu-box {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding: 50px;
    }

    .Menu-box img {
        height: 40px;
        padding-bottom: 8px;
    }

    .Menu-box span {
        font-size: 12px;
        color: #fff;
    }
}

.product-particulars {
    padding: 40px 0px;
    width: 100%;
    background-color: #FFFFFF;

    .product-particulars-conter {
        width: 900px;
        margin: 0 auto;

        .product-box {
            transition: transform 0.3s ease;
            /*  */
            transform: scale(1);
            /*  */
            text-align: center;
        }
        .product-box:hover{
            transform: scale(1.02); 
        }

        /deep/ .el-card {
            border: 0px solid #ccc;
            border-radius: 30px;
            padding: 20px;
            margin: 20px 0;
        }

        .product-content-box {
            margin: 0 auto;
            width: 900px;
            height: 100%;
            display: flex;
            flex-wrap: wrap;

            .el-button {
                background-color: #212121;
                color: #FFFFFF;
            }

            .product-content-data {
                width: 450px;
                text-align: center;

                .product-content-img {
                    background-color: #FFFFFF;
                    border-radius: 20px;
                    overflow: hidden;
                    img {
                        width: 450px;
                    }
                }

                .product-content-data-title {
                    padding: 40px 25px 0px 25px;
                    font-size: 40px;
                    color: #236EE7;
                }

                .product-content-data-txt {
                    color: #999999;
                    padding: 5px 25px 5px 25px;
                    font-size: 14px;

                }

                /deep/ .el-button.is-round {
                    padding: 8px 20px;
                }

            }
        }
    }

}
</style>



