<template>
    <div class="main">
        <!--  -->
        <div class="Secondary-Menu-bg">
            <div class="Menu-box" v-for="item in MenuList" :key="item.id" @click="newsClick(item.path)">
                <img class="Menu-hover" :src="item.imageUrl" :alt="item.name">
                <span class="Menu-hover" :class="{ active: activeItem === item.path }">{{ item.name }}</span>
            </div>
        </div>


        <!--  -->
        <div class="container">
            <div class="card-box">
                <div class="list" v-for="(item, index) in Data" :key="index" @click="goDetail(item.id)">
                    <div class="list-img">
                        <img :src="imageSrc(item.image_base64)" alt="">
                    </div>
                    <div class="content">
                        <div class="title">
                            {{ item.title }}
                        </div>
                        <div class="text">
                            <span>
                                {{ item.content }}
                            </span>
                        </div>
                        <div class="buttom">
                            <span>{{ item.time }}</span>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { getprisonBreak, getTutorial, getSkill, getRenew, getAppleNews } from '../../api/information'
import { mapState } from 'vuex';

export default {
    data() {
        return {
            Data: [],
            activeItem: 'AppleNews',
            MenuList: [
                // { path: 'Renew', id: 1, name: '', imageUrl: require('@/assets/information/machine.png') },
                // { path: 'prisonBreak', id: 2, name: '', imageUrl: require('@/assets/information/Unlock.png') },
                // { path: 'Tutorial', id: 3, name: 'MiliTools', imageUrl: require('@/assets/information/logo.png') },
                { path: 'AppleNews', id: 4, name: this.$t("information.appleNews"), imageUrl: require('@/assets/information/Apple.png') }
            ]
        }
    },
    methods: {
        goDetail(id) {
            console.log(id)
            // window.location.href = `/newsDetail?id=${id}`;
            window.open(`/newsDetail?id=${id}`, '_blank');
        },
        async newsClick(path) {
            switch (path) {
                case 'Renew':
                    // await this.getRenewData();
                    break;
                case 'prisonBreak':
                    // await this.getprisonBreakData();
                    break;
                case 'Tutorial':
                    // await this.getTutorialData();
                    break;
                case 'AppleNews':
                    await this.getAppleNewsData();
                    break;
                default:
                    break;
            }
            this.activeItem = path; // 
        },
        // async getRenewData() {
        //     const data = await getRenew();
        //     this.Data = data.res
        // },
        // async getprisonBreakData() {
        //     const data = await getprisonBreak();
        //     this.Data = data.res
        // },
        // async getTutorialData() {
        //     const data = await getTutorial();
        //     this.Data = data.res
        // },
        async getAppleNewsData() {
            const data = await getAppleNews();
            this.Data = data.res
        },
        imageSrc(data) {
            return `data:image/png;base64, ${data}`;
        },
        updateBottomTitle() {
            this.MenuList = [
                { path: 'AppleNews', id: 4, name: this.$t("information.appleNews"), imageUrl: require('@/assets/information/Apple.png') },
            ];
        },
    },
    created() {
        this.getAppleNewsData()
    },
    computed: {
        ...mapState(['language']),
    },
    watch: {
        language(newVal, oldVal) {
            console.log('Language changed. Updating bottom title...');
            if (newVal !== oldVal) {
                this.updateBottomTitle();
            }
        },
    }
}

</script>
<style scoped lang="less">
.main {
    height: 100%;
    background-color: #f7f9fc;
    width: 100%;
}

/*  */
.active {
    color: #527fda !important;
    /*  #527fda */
}


// 
.Secondary-Menu-bg {
    height: 110px;
    background: #212121;
    display: flex;
    justify-content: center;
    min-width: 1200px;

    .Menu-box {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding: 50px;

        .Menu-hover:hover+.Menu-hover {
            color: #527fda !important;
        }

        .Menu-hover:hover {
            color: #527fda !important;
        }

    }

    .Menu-box img {
        cursor: pointer;
        height: 40px;
        padding-bottom: 8px;
    }

    .Menu-box span {
        cursor: pointer;
        font-size: 12px;
        color: #fff;
    }
}

// 
.container {
    min-height: 1000px;
    display: -webkit-box;
    -webkit-box-pack: center;

    .card-box {
        padding: 10px 0;
        background-color: #fff;
        margin: 20px 0;
        display: flex;
        max-width: 1200px;
        min-width: 1200px;
        flex-wrap: wrap;
        border-radius: 10px;
        overflow: hidden;
    }

    .list:hover img {
        opacity: 0;
        transform: scale(1.1);
    }

    .list:hover .title {
        color: #527fda;
    }

    .list {
        cursor: pointer;
        border-radius: 10px;
        margin: 10px auto;
        padding: 16px;
        width: 1100px;
        height: 180px;
        border: 2px solid transparent;
        transition: border-color 0.5s ease;
        display: flex;
        transform: scale(1);

        .list-img {
            width: 250px;
            height: 180px;
            border-radius: 10px;
            overflow: hidden;

            img {
                opacity: 1;
                transition: transform 0.5s ease, opacity 0.5s ease;
                width: 250px;
                height: 180px;
            }
        }

        .content {
            margin-left: 20px;
            width: 830px;

            .title {
                font-size: 18px;
                font-weight: 600;
            }

            .text {
                width: 800px;
                margin-top: 20px;
                height: 80px;
                font-size: 14px;
                color: #999;
                line-height: 2;
                display: -webkit-box;
                -webkit-line-clamp: 3;
                -webkit-box-orient: vertical;
                overflow: hidden;
            }

            .buttom {
                margin-top: 34px;
                font-size: 12px;
                color: #999;
            }

        }
    }

    .list:hover {
        border-color: #527fda;
    }
}
</style>



