<script>
import HeadTitle from '../components/common/HeadTitle';
import { getAppleApp } from '../api/index'
import { getAppleNews } from '../api/information'

export default {
    components: { HeadTitle },
    data() {
        return {
            isShowGoTopButton: false,
            tabId: "0",//
            fileName: "",//
            hotWeekList: [],
            imgList: [
                { id: 0, idView: require('@/assets/home/carousel/banner-1.png') },
                { id: 1, idView: require('@/assets/home/carousel/banner-2.png') },
                { id: 2, idView: require('@/assets/home/carousel/banner-3.png') }
            ],
            selectList: [
                { title: '', text: '', ImgSrc: require('@/assets/home/carousel/software.png') },
                { title: '', text: '', ImgSrc: require('@/assets/home/carousel/shield.png') },
                { title: '', text: '', ImgSrc: require('@/assets/home/carousel/file.png') },
                { title: '', text: '', ImgSrc: require('@/assets/home/carousel/like.png') },
            ],
            newsImg: [
                { imgview: require('@/assets/home/01.jpg') },
                { imgview: require('@/assets/home/02.jpg') },
                { imgview: require('@/assets/home/03.jpg') },
                { imgview: require('@/assets/home/04.png') },
                { imgview: require('@/assets/home/05.jpg') }
            ],
            shopList: [
                {
                    text: 'iphone12promax 256',
                    price: '1299',
                    img: require('@/assets/home/commdity/iphone1.png')
                },
                {
                    text: 'iphone 14 promax  256g  365 ',
                    price: '1899',
                    img: require('@/assets/home/commdity/iphone2.png')
                },
                {
                    text: 'iPhone12128',
                    price: '1399',
                    img: require('@/assets/home/commdity/iphone3.png')
                },
                {
                    text: 'iphone 14 promax  512g  365 ',
                    price: '2399',
                    img: require('@/assets/home/commdity/iphone4.png')
                },
                {
                    text: 'iPhone XR 128G ID',
                    price: '999',
                    img: require('@/assets/home/commdity/iphone5.png')
                },
                {
                    text: '11iPhone11 128G ',
                    price: '799',
                    img: require('@/assets/home/commdity/iphone6.png')
                }
            ],
            AppData: [], // 
            displayedAppData: [], // 
            currentIndex: 0, // 
            displayCount: 5, // 
            AppleData: [],
        }
    },
    methods: {
        newsDetail(id) {  // 
            window.open(`/newsDetail?id=${id}`, '_blank');
        },
        goAppDetail(id) {  // app 
            window.open(`/appleAppDetail?id=${id}`, '_blank');
        },
        async getAppleAppData() {
            const AppNewsData = await getAppleApp()
            this.AppData = AppNewsData.res
            this.changeData()
        },
        changeData() {
            this.currentIndex += this.displayCount;
            if (this.currentIndex >= this.AppData.length) {
                this.currentIndex = 0; // 
            }
            this.updateDisplayedData();
        },
        updateDisplayedData() {
            this.displayedAppData = this.AppData.slice(
                this.currentIndex,
                this.currentIndex + this.displayCount
            );
        },
        handleScroll() {
            if (window.scrollY > 100) { // 100px
                this.isShowGoTopButton = true;
            } else {
                this.isShowGoTopButton = false;
            }
        },
        goTop() {
            window.scrollTo({ top: 0, behavior: 'smooth' }) // 
        },
        goinformation() {
            window.location.href = '/information';
        },
        goMall() {
            window.location.href = '/mall';
        },
        async getAppleNewsData() {
            const data = await getAppleNews();
            this.hotWeekList = data.res.slice(0, 5); // 
            console.log(this.hotWeekList);
        },
        animationSetup() {
            const selectDiv = this.$refs.selectContent;
            const skillDiv = this.$refs.skillContent;
            if (window.scrollY > 400 && selectDiv) {
                const thirdChild0 = selectDiv.children[0];
                const thirdChild1 = selectDiv.children[1];
                thirdChild0.style.display = 'block';
                thirdChild1.style.display = 'block';
                thirdChild0.classList.add('animate__animated', 'animate__fadeInUp');
                thirdChild1.classList.add('animate__animated', 'animate__fadeInUp');
            }
            if (window.scrollY > 700 && selectDiv) {
                const thirdChild2 = selectDiv.children[2];
                const thirdChild3 = selectDiv.children[3];
                thirdChild2.style.display = 'block';
                thirdChild3.style.display = 'block';
                thirdChild2.classList.add('animate__animated', 'animate__fadeInUp');
                thirdChild3.classList.add('animate__animated', 'animate__fadeInUp');
            }
            if (window.scrollY > 1700 && skillDiv) {
                const skillChild0 = skillDiv.children[0];
                skillChild0.classList.add('g00', 'animate__animated', 'animate__fadeInUp');
                const skillChildR1 = skillDiv.children[1].children[0];
                skillChildR1.classList.add('g02', 'animate__animated', 'animate__fadeInUp');
                const skillChildR12 = skillDiv.children[1].children[1];
                skillChildR12.classList.add('g1', 'animate__animated', 'animate__fadeInUp');
                // skillDiv.classList.add('animate__animated', 'animate__fadeInUp');
                skillDiv.style.opacity = '1';
            }
        }
    },
    created() {
        // this.getAppleAppData();
        // this.getAppleNewsData()
    },
    mounted() {
        // window.addEventListener('scroll', this.handleScroll)
        window.addEventListener('scroll', this.animationSetup)
    },
    beforeDestroy() {
        // window.removeEventListener('scroll', this.handleScroll);
        window.addEventListener('scroll', this.animationSetup)
    }
}
</script>

<style scoped lang="less">
.svgname {
    width: 310px;
    height: 410px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.main {
    overflow-y: hidden;
}

.g00 {
    animation-delay: 10ms;
}

.g02 {
