}

.g1 {
    animation-delay: 300ms;
}

.g2 {
    animation-delay: 600ms;
}

.g3 {
    animation-delay: 900ms;
}

.g4 {
    animation-delay: 1200ms;
}

.skill {
    background-color: #000000;
    color: #fff;
    // text-align: center;
    padding-bottom: 100px;
    height: 720px;

    .skill-width {
        width: 1400px;
        margin: 0 auto;
        min-width: 1400px;
        max-width: 100%;

        .up {
            text-align: center;

            h1 {
                font-size: 60px;
                font-weight: 500;
            }

            p {
                color: #666;
                padding: 20px 0 100px 0;
            }

        }

        .content {
            opacity: 0;
            margin: 0 auto;
            height: 400px;
            display: flex;
            justify-content: center;
            width: 1000px;
            min-width: 1000px;

            h1 {
                font-weight: 500;
                font-size: 26px;
                padding: 20px 0 0 40px;
            }

            img {
                height: 40px;
                margin-top: 20px;
                margin-left: 40px;

            }

            span {
                margin-top: 20px;
                display: inline-block;
                width: 300px;
                color: #666;
                margin-left: 40px;
                font-size: 12px;
                line-height: 20px;
            }

            .round:hover i {
                color: #527fda;
            }

            .round {
                cursor: pointer;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 40px;
                height: 40px;
                background-color: #fff;
                border-radius: 50px;



                i {
                    color: #000000;
                    font-weight: 700;
                    font-size: 30px;
                }
            }

            .left {
                position: relative;
                width: 490px;
                height: 400px;
                border-radius: 10px;
                background-color: #161616;

                .round {
                    position: absolute;
                    bottom: 30px;
                    right: 30px;

                }

                p {
                    padding-top: 250px;
                }
            }

            .right {
                margin-left: 20px;
                height: 400px;
                width: 490px;
                // background-color: #2f3850;

                div:nth-child(1) {
                    position: relative;
                    border-radius: 10px;
                    height: 190px;
                    background-color: #1d1d1d;

                    .round {
                        position: absolute;
                        bottom: 30px;
                        right: 30px;

                    }
                }

                div:nth-child(2) {
                    position: relative;
                    margin-top: 20px;
                    border-radius: 10px;
                    height: 190px;
                    background-color: #1d1d1d;

                    .round {
                        position: absolute;
                        bottom: 30px;
                        right: 30px;

                    }

                }

            }
        }
    }

}


.select {
    background-color: #000000;
    color: #fff;
    padding-bottom: 100px;

    .select-width {
        width: 1400px;
        margin: 0 auto;
        padding-top: 300px;
        min-width: 1400px;
        max-width: 100%;

        .up {
            text-align: center;

            h1 {
                font-size: 60px;
                font-weight: 500;
            }

            p {
                color: #666;
                padding: 20px 0 100px 0;
            }
        }

        .content {
            width: 1400px;
            height: 880px;
            display: flex;
            flex-wrap: wrap;
            text-align: center;
            background-repeat: no-repeat;
            background-position: 70% 20%;
            background-image: url('../assets/home/carousel/bg1.png');

            div {
                display: none;
            }

            .content-box {
                width: 700px;
                height: 400px;
                margin-top: 40px;

                .img-box {
                    img {
                        height: 70px;
                    }
                }

                h1 {
                    margin: 40px 0 20px 0;
                    font-size: 24px;
                }

                div {
                    display: flex;
                    justify-content: center;

                    p {
                        color: #666;
                        width: 320px;
                        line-height: 50px;
                    }
                }
            }
        }
    }
}

.radial-gradient-demo {
    z-index: 99;
    position: absolute;
    width: 1000px;
    min-height: 800px;
    background-repeat: no-repeat;
    // background-size: 800px,800px;
    background-position: -600px 50px, -200px 0, -150px 150px;
    background-image:
        radial-gradient(closest-side, rgba(255, 100, 100, 0.292), rgba(0, 0, 0, 0)),
        radial-gradient(closest-side, rgba(0, 106, 255, 0.331), rgba(0, 0, 0, 0)),
        radial-gradient(closest-side, rgb(0, 0, 0), rgba(0, 0, 0, 0));
    animation: 15s change infinite;
}

@keyframes change {

    0% {
        background-position: -600px 50px, -200px 0, -150px 150px;
    }

    25% {
        background-position: -500px 100px, -200px 0, -150px 150px;
    }

    50% {
        background-position: -400px 50px, -200px 0, -150px 150px;
    }

    75% {
        background-position: -500px 0px, -200px 0, -150px 150px;
    }

    100% {
        background-position: -600px 50px, -200px 0, -150px 150px;
    }

}

.banner-box {
    background-color: #000000;
}

.banner {
    margin: 0 auto;
    width: 1400px;
    min-width: 1400px;
    min-height: 600px;
    height: 600px;
    padding-top: 400px;
    display: flex;
    justify-content: space-between;

    .banner-left {
        z-index: 999;
        width: 60%;
        // margin-left: 10%;

        .cls-1 {
            fill: #fff;
        }

        .cls-2 {
            fill: #4b4b52;
            // animation: color-change 4s infinite alternate;
        }

        .ipad {
            width: 330px;
            height: 430px;
        }

        .iphone {
            height: 300px;
            width: 150px;
            margin-left: 50px;
        }

        .ipod {
            width: 90px;
            height: 160px;
            margin-left: 50px;
        }

        .ipodnano {
            margin-left: 50px;
            width: 80px;
            height: 80px;
        }

        @keyframes color-change {
            0% {
                fill: #2f3850;
            }

            100% {
                fill: #5b5b67;
            }
        }
    }

    .banner-right {
        color: #ffffff;
        margin-top: 65px;

        .right-box {
            margin-right: 20px;

            .title {
                font-weight: 600;
                font-size: 100px;
                width: 400px;
            }

            .subtitle {
                margin: 50px 0 80px 0;
                font-size: 30px;
                font-weight: 600;
            }

            .banner-btn {
                border-radius: 50px;
                border: 2px solid #ffffff;
                background-color: #fff;
                width: 300px;
                height: 50px;
                font-weight: 700;
                font-size: 30px;
                color: #000000;
                height: 60px;
            }

            .banner-btn:hover {
                background-color: #ffffff;
                color: #527fda;
            }
        }
    }
}

.carousel-text {
    position: absolute;
    bottom: 25px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 20px;
    font-weight: 600;
    background-color: rgba(255, 255, 255, 0.3);
    white-space: nowrap;
    backdrop-filter: blur(5px);
    padding: 10px;
    border-radius: 20px;
}

.block {
    margin: 120px 0 10px 0;
    min-width: 1200px;

    .banner-img {
        object-fit: scale-down;
        width: 100%;
        height: 100%;
    }
}

.main {
    position: relative;
    height: 100%;
    overflow-x: hidden;
    background-color: #fff;
    width: 100%;
}

.el-row {
    &:last-child {
        margin-bottom: 0;
    }
}

.bg-purple-dark {
    min-width: 1240px;
    background: #212121;

    .header {
        margin: 0 auto;
        width: 1240px;
        height: 96px;
        display: flex;

        .logo {
            margin-top: 20px;
            overflow: hidden;
            width: 256px;
            font-size: 18px;
            text-align: center;
            display: flex;
            justify-content: flex-end;
            color: #FFFFFF;
            font-family: 'Adobe Heiti Std';
            padding: 0 60px;

            img {
                height: 56px;
                width: 56px;
            }

            .txt {
                display: inline-block;
                line-height: 62px;
                margin-left: 15px;
            }
        }

        .navigation {
            margin-top: 28px;
            height: 38px;

            .tab-list {
                width: 200px;
            }

            /deep/.el-tabs__item {
                font-size: 16px;
                color: #FFFFFF;
            }

            /deep/ .is -active {
                color: #FFFFFF;
                font-weight: bold;
            }

            /deep/.el-tabs__active-bar {
                background-color: #FFFFFF;
            }

            /deep/.el-tabs__nav-wrap::after {
                height: 0px;
            }

        }

        .search {
            margin: 30px 0 0 50px;
            width: 300px;
            text-align: center;

            /deep/ .el-input--prefix .el-input__inner {
                border-radius: 25px;
                border-color: none;
            }
        }
    }
}



.product-center {
    .product-headht {
        min-width: 1200px;
        background-color: #212121;
        text-align: center;
        height: 118px;
        padding-top: 45px;

        .product-title {
            cursor: default;
            color: #ffffff;
            font-size: 41px;
        }

        .product-txt {
            color: #CCCCCC;
            font-size: 19px;
        }
    }

    .product-content {
        background-color: #212121;
        padding: 100px 0;

        .product-content-box {
            margin: 0 auto;
            width: 1200px;
            min-width: 1200px;
            height: 100%;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-around;

            .Download_Title_2:hover .product-content-data-title {
                color: #527fda !important;
            }

            .Download_Title_1:hover .product-content-data-title {
                color: #527fda !important;
            }

            .product-content-data {
                width: 450px;
                text-align: center;
                // margin-bottom: 100px;

                .product-content-img {
                    background-color: #FFFFFF;
                    border-radius: 20px;
                    overflow: hidden;
                    height: 250px;

                    img {
                        width: 450px;
                    }
                }

                .product-content-data-title {
                    padding: 40px 25px 0 25px;
                    font-size: 40px;
                    cursor: pointer;
                    color: #ffffff;
                }

                .product-content-data-txt {
                    cursor: pointer;
                    color: #999999;
                    padding: 20px 25px 20px 25px;
                    font-size: 14px;
                }

                /deep/ .el-button.is-round {
                    padding: 8px 20px;
                }

            }
        }
    }
}

.commodity-center {
    width: 100%;

    .commodity-headht {
        background-color: #FFFFFF;
        min-width: 1200px;
        text-align: center;
        // height: 120px;
        padding: 50px 0;

        .commodity-title {
            cursor: default;
            color: #212121;
            font-size: 41px;
        }

        .commodity-txt {
            color: #CCCCCC;
            font-size: 19px;
        }
    }

    .commodity-content {
        width: 1200px;
        margin: 0 auto;
        padding-bottom: 50px;

        .more-goods {
            color: #000000;
            font-size: 14px;
            display: flex;
            margin-right: 24px;
            justify-content: space-between;

            .moerlink:hover {
                cursor: pointer;
                color: #527fda;
                font-weight: 600;
            }
        }

        .more-list {
            display: flex;
            flex-wrap: wrap;

            .more-list-data:hover img {
                transform: scale(1.1);
            }

            .more-list-data:hover {
                box-shadow: 0 0 6px 2px #b3bad6;
                color: #527fda;
            }

            .more-list-data {
                cursor: pointer;
                margin: 10px 10px;
                width: 240px;
                background-color: #fff;
                border-radius: 10px;
                padding: 20px;
                height: 300px;

                div {
                    overflow: hidden;
                    border-radius: 10px;
                }

                img {
                    transition: transform 0.3s ease;
                    width: 240px;
                    height: 240px;
                    // margin: 10px;
                    border-radius: 10px;
                }

                .commdity-item-name {
                    margin: 10px 0;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    height: 32px;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                    font-size: 12px;
                }

                .commdity-jiage {
                    // font-size: 14px;
                    margin-top: 10px;
                    // text-align: center;

                    .commdity-jiagename {
                        // color: #FF4300;
                    }

                    .jiage {
                        // color: #FF4300;
                        font-weight: 600;
                        // text-align: left;
                    }
                }
            }
        }
    }
}

.hot_spot_consultation {
    width: 100%;
    background-color: #212121;

    .hot_spot_consultation-headht {
        text-align: center;
        min-width: 1200px;
        padding: 50px;
        color: #FFFFFF;

        .hot_spot_consultation-title {
            cursor: default;
            font-size: 41px;
        }

        .hot_spot_consultation-txt {
            font-size: 20px;
        }
    }

    .hot_spot_consultation-conter {
        display: flex;
        justify-content: space-between;
        margin: 0 auto;
        width: 1200px;
        height: 500px;
        min-width: 1200px;

        .hot_spot_consultation-carousel {
            width: 880px;
            border-radius: 10px;
            overflow: hidden;
            cursor: pointer;
        }

        .hot_spot_consultation-hot_week {
            width: 280px;
            color: #FFFFFF;

            .hot_week-title {
                font-size: 14px;
            }

            .hot_week-conter {
                margin: 20px 0;

                .hot_week-list {
                    cursor: pointer;
                    display: flex;
                    margin-bottom: 15px;

                    .hot_week-list_index {
                        color: #7E7E7E;
                    }

                    .hot_week-list_data {
                        overflow: hidden;

                        .hot_week-list_title:hover {
                            color: #93b9ff;
                        }

                        .hot_week-list_title {
                            height: 38px;
                            color: #fff;
                            margin-left: 15px;
                            width: 250px;
                            word-wrap: break-word;
                            font-size: 14px;
                            overflow: hidden;
                            display: -webkit-box;
                            -webkit-line-clamp: 2;
                            -webkit-box-orient: vertical;
                        }

                        .hot_week-list_data-boot {
                            display: flex;
                            margin: 10px;
                            margin-top: 6px;
                            font-size: 11px;
                            font-weight: 400;
                            justify-content: space-between;
                        }
                    }
                }
            }

            .hot_week-but {
                .el-button {
                    width: 280px;
                    background: none;
                    font-weight: 600;
                }

                .el-button--primary.is-plain {
                    color: #fff;
                    border-color: #fff;
                }

                .el-button:hover {
                    // border-color: #fff;
                    // background: #527fda;
                    background: #fff;
                    color: #000000;

                }
            }
        }
    }

    .hot_topic {
        width: 1200px;
        margin: 50px auto;

        .hot_topic-headht {
            display: flex;
            font-size: 14px;
            margin-bottom: 10px;
            justify-content: space-between;
        }

        .changeApp {
            color: #999;
        }

        .changeApp:hover {
            cursor: pointer;
            color: #fff;
        }

        .hot_topic-list {
            cursor: pointer;
            display: flex;
            justify-content: space-between;

            .hot_topic-list-data:hover .hot_topic-list-data-conter-title {
                color: #93b9ff !important;
            }

            .hot_topic-list-data:hover .hot_topic-list-data-conter-txt {
                color: #93b9ff !important;
            }

            .hot_topic-list-data {

                margin-top: 20px;
                width: 210px;
                display: flex;

                img {
                    width: 60px;
                    height: 60px;
                    border-radius: 6px;
                }

                .hot_topic-list-data-conter {
                    margin-left: 10px;

                    .hot_topic-list-data-conter-title {
                        color: #fff;
                        font-size: 14px;
                        white-space: nowrap;
                        overflow: hidden;
                        width: 140px;
                        text-overflow: ellipsis;
                    }

                    .hot_topic-list-data-conter-txt {
                        overflow: hidden;
                        display: -webkit-box;
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                        color: #999999;
                        margin-top: 8px;
                        font-size: 12px;
                    }
                }
            }
        }

    }
}

.slot-title {
    font-size: 40px;
    color: red;
    letter-spacing: 20px;
}

.slot-subtitle {
    color: #FF4300;
}



/deep/ .el-button--primary {
    color: #000000;
    background-color: #fff;
}

.container {
    z-index: 999999999999;
    position: fixed;
    right: 3%;
    bottom: 6%;
    width: 80px;
    height: 80px;
    color: #fff;

    .runter-top {
        width: 80px;
        height: 80px;
    }
}
</style>


